@font-face {
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Manrope-Regular.woff2") format("woff2"), url("../fonts/Manrope-Regular.woff") format("woff");
}
@font-face {
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Manrope-Medium.woff2") format("woff2"), url("../fonts/Manrope-Medium.woff") format("woff");
}
@font-face {
  font-family: "Manrope", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Manrope-Bold.woff2") format("woff2"), url("../fonts/Manrope-Bold.woff") format("woff");
}
html,
body {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}

body {
  position: relative;
  max-height: 100%;
  width: 100%;
  font-size: 14px;
  line-height: 30px;
  font-family: "Manrope", sans-serif;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

a {
  color: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  margin: 0 auto;
  padding-left: 25px;
  padding-right: 25px;
  max-width: 1440px;
}
@media (min-width: 768px) {
  .container {
    padding-left: 40px;
    padding-right: 40px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding-left: 60px;
    padding-right: 60px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.navigation {
  display: flex;
  justify-content: end;
  align-items: center;
}

.navigation__ul {
  display: flex;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0;
  list-style: none;
}

.navigation__li {
  display: flex;
  align-items: center;
  min-height: 48px;
  margin-left: 35px;
  cursor: pointer;
  user-select: none;
}
.navigation__li:first-child {
  margin-left: 0;
}

.navigation__li a {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #0E091B;
  text-decoration: none;
}

.navigation__li a::before,
.navigation__li a::after {
  display: inline-block;
  opacity: 0;
  transition: transform 0.3s, opacity 0.2s;
}

.navigation__li a::before {
  margin-right: 10px;
  content: "[";
  transform: translateX(20px);
}

.navigation__li a::after {
  margin-left: 10px;
  content: "]";
  transform: translateX(-20px);
}

.navigation__li a:hover::before,
.navigation__li a:hover::after,
.navigation__li a:focus::before,
.navigation__li a:focus::after {
  opacity: 1;
  transform: translateX(0px);
}

.animation {
  background-size: 100%;
  background-position: center;
  position: relative;
  padding: 15px 0 115px;
  background-color: #f9f9f9;
}

.animation__ul {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}

.animation__title {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #0E091B;
}

.animation__gif {
  position: absolute;
  top: 50px;
  width: 100px;
  height: 200px;
}

.animation__gif img {
  width: 100%;
  text-align: center;
  border-radius: 50%;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.2117647059);
}

.animation__link-p {
  display: block;
  text-decoration: none;
  cursor: pointer;
}

.animation__link-paragraph {
  font-size: 22px;
  line-height: 32px;
  color: #253d5f;
  margin-bottom: 30px;
}

.animation__service-block {
  background: #fff;
  border: 1px solid #eff7ff;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  z-index: 5;
  padding: 180px 30px 25px 40px;
  box-shadow: 0px 2px 10px 0px rgba(12, 0, 46, 0.06);
  border: 1px solid #e6e4e4;
}

.animation__service-block:before {
  content: "";
  width: 273px;
  height: 273px;
  background: #00a86b;
  border-radius: 50%;
  position: absolute;
  top: -148px;
  left: -83px;
  transform: scale(0);
}

.animation__service-block:before,
.animation__service-block .hover-content:before,
.animation__service-block .hover-content:after {
  will-change: transform;
}

.animation__service-block:hover:before {
  transform: scale(1);
  transition: all 0.5s linear;
}

.animation__service-block:hover {
  border-color: transparent;
}

.animation .animation__li {
  margin-bottom: 30px;
  max-width: 400px;
}

.animation .animation__service-block .hover-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #006a4e;
  z-index: -3;
  opacity: 0;
  transition: all 0.3s linear;
}

.animation .animation__service-block:hover .hover-content {
  opacity: 1;
}

.animation .animation__service-block .hover-content:before {
  content: "";
  width: 493px;
  height: 493px;
  background: #119462;
  border-radius: 50%;
  position: absolute;
  top: -250px;
  left: -180px;
  z-index: 1;
  transform: translate(-50%, -50%);
  opacity: 0;
}

.animation .animation__service-block:hover .hover-content:before {
  opacity: 1;
  transform: translate(0, 0);
  transition: all 0.9s linear;
}

.animation .animation__service-block .hover-content:after {
  content: "";
  width: 602px;
  height: 602px;
  background: #007f5c;
  border-radius: 50%;
  position: absolute;
  top: -196px;
  left: -180px;
  z-index: -1;
  transform: translate(-50%, -50%);
  opacity: 0;
}

.animation .animation__service-block:hover .hover-content:after {
  opacity: 1;
  transform: translate(0, 0);
  transition: all 1.3s linear;
}

.animation .animation__service-block .animation__snow-dot {
  position: absolute;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
  background: rgba(255, 255, 255, 0.5);
  z-index: -1;
}

.animation .animation__service-block .animation__snow-dot:nth-child(1) {
  top: 25px;
  left: 41%;
  z-index: 1;
  animation: scale-upOne 1s infinite ease-in-out;
  animation-delay: 0s;
}

.animation .animation__service-block .animation__snow-dot:nth-child(2) {
  top: 25px;
  left: 78%;
  animation: scale-upOne 1.1s infinite ease-in-out;
  animation-delay: 0.1s;
}

.animation .animation__service-block .animation__snow-dot:nth-child(3) {
  top: 75px;
  left: 61%;
  animation: scale-upOne 1.2s infinite ease-in-out;
  animation-delay: 0.6s;
}

.animation .animation__service-block .animation__snow-dot:nth-child(4) {
  top: 136px;
  left: 84%;
  animation: scale-upOne 0.95s infinite ease-in-out;
  animation-delay: 0.2s;
}

.animation .animation__service-block .animation__snow-dot:nth-child(5) {
  bottom: 35px;
  right: 12%;
  animation: scale-upOne 0.9s infinite ease-in-out;
  animation-delay: 0.4s;
}

.animation .animation__service-block .animation__snow-dot:nth-child(6) {
  bottom: 80px;
  right: 31%;
  animation: scale-upOne 0.7s infinite ease-in-out;
  animation-delay: 0.3s;
}

.animation .animation__service-block .animation__snow-dot:nth-child(7) {
  bottom: 40px;
  right: 51%;
  animation: scale-upOne 1.2s infinite ease-in-out;
  animation-delay: 0.9s;
}

.animation__service-block:hover .title a,
.animation__service-block:hover p {
  color: #fff;
}

@keyframes scale-upOne {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.2);
  }
}